import React from 'react'

function Card({ img, title, body }) {
    return (
        <div class="card m-4 shadow" style={{ width: "18rem" }}>
            <img src={img} style={{ height: '16rem' }} class="card-img-top" alt="..." />
            <div class="card-body">
                <h5 class="card-title">{title}</h5>

                <p class="card-text">{body}</p>

            </div>
        </div>
    )
}

export default Card
